// pages/dashboard.js
import Layout from '../components/Layout';
import { useAuth } from '../context/AuthContext';
import { useRouter } from 'next/router';
import { useEffect } from 'react';

export default function Dashboard() {
  const { isLoggedIn, loading: authLoading } = useAuth();
  const router = useRouter();

  useEffect(() => {
    if (!authLoading && !isLoggedIn) router.push('/login');
  }, [authLoading, isLoggedIn, router]);

  if (authLoading || !isLoggedIn) {
    return (
      <Layout>
        <div className="flex-1 grid place-items-center">
          <p className="text-gray-400">Loading...</p>
        </div>
      </Layout>
    );
  }

  // Demo dashboard content
  const demoLinks = [
    { title: 'Start a new chat', href: '/chat' },
    { title: 'See information', href: '/info' },
    { title: 'DLIMS status', href: '/chat' },
    { title: 'NADRA services', href: '/chat' },
  ];

  return (
    <Layout>
      <section className="mx-auto max-w-6xl px-4 py-8">
        <h1 className="text-2xl md:text-3xl font-bold text-gray-100">Welcome back</h1>
        <p className="mt-1 text-gray-400">This is your dashboard. Explore features and quick links.</p>

        <div className="mt-6 grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
          <div className="rounded-2xl border border-emerald-900/40 bg-gray-900/60 p-5">
            <h3 className="text-lg font-semibold text-gray-100">Your Profile</h3>
            <dl className="mt-3 space-y-1 text-sm text-gray-400">
              <div className="flex justify-between"><dt>Username</dt><dd>Guest</dd></div>
              <div className="flex justify-between"><dt>Status</dt><dd>Online</dd></div>
              <div className="flex justify-between"><dt>Member since</dt><dd>2025</dd></div>
            </dl>
          </div>

          <div className="rounded-2xl border border-emerald-900/40 bg-gray-900/60 p-5">
            <h3 className="text-lg font-semibold text-gray-100">Usage</h3>
            <p className="mt-2 text-sm text-gray-400">You have 0 chats today. Create one to get started.</p>
          </div>

          <div className="rounded-2xl border border-emerald-900/40 bg-gray-900/60 p-5">
            <h3 className="text-lg font-semibold text-gray-100">Tips</h3>
            <ul className="mt-2 list-disc pl-5 text-sm text-gray-400">
              <li>Ask in Urdu or English.</li>
              <li>Prefix with a department, e.g., ‚ÄúNADRA: family registration‚Äù.</li>
            </ul>
          </div>
        </div>

        <h2 className="mt-8 text-xl font-semibold text-gray-100">Quick links</h2>
        <div className="mt-3 grid gap-3 sm:grid-cols-2 md:grid-cols-3">
          {demoLinks.map(link => (
            <a key={link.title} href={link.href} className="rounded-xl border border-emerald-900/40 bg-gray-900/60 p-4 text-sm text-gray-300 hover:border-emerald-800 hover:text-white transition-colors">{link.title}</a>
          ))}
        </div>
      </section>
    </Layout>
  );
}